import React, { useState, useEffect, useCallback } from "react";
import { ProductModal } from "../ProductModal/ProductModal";
import { VideoModal } from "../VideoModal/VideoModal";
import { ARModal } from "../ARModal/ARModal";

function ModalContainerComponent() {
  const [openVariant, setOpenVariant] = useState(false);
  const [variant, setVariant] = useState(null);
  const [modal, setModal] = useState(null);

  const PRODUCTS = require("../../products.json");
  const VARIATIONS = require("../../variations.json");
  const IMAGES = require.context("../../../public/product-images", true, /\.jpg$/);

  const findProduct = useCallback((products, model, country) => {
    return products.find((product) => product.Model === model && product.Site === country);
  }, []);

  function getKeySpecs(product) {
    let detail = Object.keys(product)
      .filter((keyspec) => keyspec.startsWith("key spec"))
      .map((e) => product[e]);
    return detail;
  }

  function getVariations(model, country) {
    return findProduct(VARIATIONS, model, country)?.Variations || [];
  }

  function getIndications(indication) {
    return indication.split("\n");
  }

  const [openVideo, setOpenVideo] = useState(false);
  const openVideoModal = () => setOpenVideo(true);
  const closeVideoModal = () => setOpenVideo(false);

  const [openAR, setOpenAR] = useState(false);
  const openARModal = () => setOpenAR(true);
  const closeARModal = () => setOpenAR(false);

  const handleClose = () => {
    setVariant(null);
    setModal(null);
  };

  useEffect(() => {
    function callback(e) {
      if (e.data.model) {
        const targetProduct = findProduct(PRODUCTS, e.data.model, e.data.country);
        setModal(targetProduct);
      }
    }
    window.addEventListener("message", callback);

    return () => {
      window.removeEventListener("message", callback);
    };
  }, [PRODUCTS, findProduct]);

  return (
    <>
      <VideoModal openVideo={openVideo} closeVideoModal={closeVideoModal} findProduct={findProduct} modal={modal} setModal={setModal} />
      <ProductModal
        PRODUCTS={PRODUCTS}
        IMAGES={IMAGES}
        openVideoModal={openVideoModal}
        findProduct={findProduct}
        getVariations={getVariations}
        getKeySpecs={getKeySpecs}
        getIndications={getIndications}
        handleClose={handleClose}
        variant={variant}
        setVariant={setVariant}
        openVariant={openVariant}
        setOpenVariant={setOpenVariant}
        modal={modal}
        openARModal={openARModal}
      />
      <ARModal openAR={openAR} closeARModal={closeARModal} findProduct={findProduct} modal={modal} setModal={setModal} />
    </>
  );
}

const ModalContainer = React.memo(ModalContainerComponent)

export { ModalContainer };
