import React, { useState } from "react";
import { Link } from "react-router-dom";
import styles from "./CountryDropdown.module.scss";
import { navLinks } from "../../constants/navLinks";

const CountryDropdown = () => {
  const [open, setOpen] = useState(false);
  const toggleDropdown = () => setOpen(!open);

  // const location = useLocation();

  // let currentCountry;

  // if (location && location.pathname) {
  //   currentCountry = navLinks.find((route) => route.route === location.pathname)?.country;
  // }

  return (
    <>
      <div className={styles.navDropdownBtn}>
        <div className={styles.selectContainer}>
          <p>Country</p> <i className="fas fa-caret-down" onClick={toggleDropdown}></i>
        </div>
        {open && (
          <div className={styles.countryDropdown} role="menu">
            {navLinks.map((route, index) => (
              <Link to={route.route} className={styles.dropdownItem} onClick={toggleDropdown} key={index} role="menuitem">
                {route.country}
              </Link>
            ))}
          </div>
        )}
      </div>
    </>
  );
};

export { CountryDropdown };
