import { useEffect, useMemo } from "react";
import { Route, Switch, useLocation } from "react-router-dom";
import "./App.css";
import { Footer } from "./components/Footer/Footer";
import { ModalContainer } from "./components/ModalContainer/ModalContainer";
import { Sidebar } from "./components/Sidebar/Sidebar";
import { Malaysia } from "./pages";
import { routes } from "./constants/routes";
import { goFullScreen, exitFullscreen } from "./utils/fullscreenHelper";
import { useWindowSize } from "./utils/newScreenHeightHelper";

// const getDomain =
//   process.env.NODE_ENV === "development"
//     ? "http://localhost:3000/"
//     : "https://panasonic.com/";

const getVistaUrl = (country) => {
  const urlSwitch = {
    malaysia: "/space-solution/malaysia/malaysia_r27/index.htm",
  };

  return urlSwitch[country];
};

function App() {
  const [windowWidth, windowHeight] = useWindowSize();

  useEffect(() => {
    function callback(e) {
      if (e.data.fullscreen) {
        goFullScreen();
      } else if (e.data.exit) {
        exitFullscreen();
      }
    }
    window.addEventListener("message", callback);

    return () => {
      window.removeEventListener("message", callback);
    };
  }, []);

  const location = useLocation();
  return (
    <div className="App" style={{ height: windowHeight, width: windowWidth }}>
      <Sidebar />
      <ModalContainer />
      <Switch>
        <Route exact path={routes.my} render={() => <Malaysia getVistaUrl={getVistaUrl} />}></Route>
      </Switch>
      {/* <Switch>
        <Route exact path={routes.sg} component={() => <Singapore getVistaUrl={getVistaUrl} />}></Route>
      </Switch> */}
      {location.pathname === "/" ? "" : <Footer />}
    </div>
  );
}

export default App;
